#product-table {
    border: 1px solid #e5e5e5;
    box-shadow: 0 6px 25px 3px rgb(0 0 0 / 20%);
    display: table;
    font-family: "Roboto";
    margin: 0 auto;
    width: 100%;
    .product-table-head {
        display: table-header-group;
        height: 60px;
        width: auto;
        .product-table-row {
            .column {
                background-color: #f6f7f9;
                color: #767676;
                border-bottom: 1px solid #e5e5e5;
                padding: 0 5px;
                text-align: center;
            }
        }
    }
    .product-table-body {
        display: table-row-group;
        .product-table-row {
            height: 85px;
            padding: 0;
        }
    }
    .product-table-row {
        display: table-row;
        &:hover {
            background-color: #F6F7F9;
        }
        .column {
            display: table-cell;
            width: auto;
            border-right: 1px solid #e5e5e5;
            vertical-align: middle;
            text-align: left;
            padding: 5px;
            border-bottom: 2px solid #e5e5e5;
            .product-table-number {
                display: flex;
                flex-direction: column;
                align-items: center;
                line-height: 17px;
                width: 55px;
                span {
                    color: #7c284e;
                    text-align: center;
                    line-height: 17px;
                    font-size: 18px;
                }
                small {
                    color: #b9b9b9;
                    font-size: 13px;
                }
            }
            a img {
                padding: 0 10px;
                width: 80px;
                height: 80px;
            }
            &.column-title a {
                color: #767676;
                display: block;
                text-align: left;
                text-decoration: none;
                padding-right: 30px;
                max-width: 150px;
                width: 100%;
                padding-left: 7px;
            }
            &:nth-child(4) {
                width: 100px;
                margin: 0 auto;
                span {
                    width: 60px;
                    text-align: center;
                }
            }
            &:last-child {
                min-width: 190px;
                .product-table-button {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    a {
                        position: relative;
                        font-size: 12px;
                        text-decoration: none;
                        text-transform: uppercase;
                        width: 140px;
                        background-color:#7c284e;
                        text-align: center;
                        border-radius: 30px;
                        span {
                            position: relative;
                            color: white;
                            line-height: 32px;
                            white-space: pre;
                            z-index: 1;
                        }
                    }
                    &:hover {
                        a {
                            background-color: #4a001c;
                        }
                    }
                    &.sale a {
                        border: 3px solid #ff9000;
                        position: relative;
                        &::before {
                            content: "";
                            background-image: url('../img/discount-badge.png');
                            background-position: center;
                            background-repeat: no-repeat;
                            background-size: contain;
                            width: 24px;
                            height: 24px;
                            position: absolute;
                            top: -10px;
                            left: -10px;
                            z-index: 1;
                        }
                        &::after {
                            content: "";
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            bottom: 0;
                            border-radius: 30px;
                            border: 1px solid #fff;
                        }
                    }
                    span {
                        display: block;
                        color: #767676;
                        font-size: small;
                        line-height: 1.4;
                        margin-top: 3px;
                    }
                }
            }
            .product-table-rating {
                display: flex;
                flex-direction: column;
                align-items: center;
                span {
                    color: #767676;
                    font-size: 32px;
                    font-weight: 300;
                    line-height: 1.2;
                }
                a {
                    color: #767676;
                    font-size: x-small;
                    line-height: 1.2;
                    text-transform: uppercase;
                    text-decoration: none;
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
        &.highlighted-row {
            background-color: rgb(1, 151, 188);
            .column {
                .product-table-number {
                    span {
                        color: #d84343;
                    }
                    small {
                        color: white;
                    };
                }
                &:nth-child(3) a {
                    color: white;
                }
                .product-table-rating {
                    span {
                        color: white;
                    }
                    a {
                        color: white;
                    }
                }
                &:last-child {
                    .product-table-button {
                        a {
                            background-color: #d84343;
                        }
                        &:hover {
                            a {
                                background-color: #982F2F;
                            }
                        }
                        span {
                            color: white;
                        }
                    }
                }
            }
        }
    }
}
@media (max-width: 991px) {
    #product-table {
        width: 100%;
        box-shadow: none;
        border-collapse: separate;
        border-spacing: 0 40px;
        border: none;
        .product-table-head {
            display: none;
        }
        .product-table-body {
            border-bottom: none;
            .product-table-row {
                border-bottom: none;
                box-shadow: 0 6px 25px 3px rgb(0 0 0 / 20%);
                padding: 20px;
                &:hover {
                    background-color: #fff;
                }
                &.highlighted-row {
                    &:hover {
                        background-color: rgb(1, 151, 188);
                    }
                }
                .column {
                    display: flex;
                    justify-content: center;
                    border-bottom: 0;
                    border-right: 0;
                    a {
                        text-align: center;
                    }
                    .product-table-number {
                        width: 30px;
                        background-color: #7c284e;
                        padding: 5px;
                        border-radius: 5px;
                        span {
                            color: white;
                        }
                        small {
                            color: white;
                        }
                    }
                    &:nth-child(2) {
                        padding-top: 30px;
                    }
                    .product-table-button {
                        a {
                            border-radius: 5px;
                        }
                        &.sale {
                            a {
                                border-radius: 30px;
                            }
                        }
                    }
                }
                .column.column-title {
                    a {
                        padding: 5px;
                    }
                }
                .column:first-child {
                    position: absolute;
                    padding-top: 20px;
                    padding-left: 20px;
                }
                .column:last-child {
                    border-bottom: none;
                    padding-bottom: 30px;
                }
                &.highlighted-row {
                    .product-table-number {
                        border: 1px solid #fff;
                        span {
                            color: #ff9000;
                        }
                    }
                }
            }
        }
    }
}